@font-face {
  font-family: 'Gilroy';
  src: url("../../fonts/Gilroy/Gilroy.eot");
  src: local("Gilroy"), url("../../fonts/Gilroy/Gilroy.woff") format("woff"), url("../../fonts/Gilroy/Gilroy.ttf") format("truetype"), url("../../fonts/Gilroy/Gilroy.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy-Bold';
  src: url("../../fonts/Gilroy-Bold/Gilroy-Bold.eot");
  src: local("Gilroy"), url("../../fonts/Gilroy-Bold/Gilroy-Bold.woff") format("woff"), url("../../fonts/Gilroy-Bold/Gilroy-Bold.ttf") format("truetype"), url("../../fonts/Gilroy-Bold/Gilroy-Bold.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'PTSans';
  src: url("../../fonts/PTSans/pt-sans.eot");
  src: local("PTSans"), url("../../fonts/PTSans/pt-sans.woff") format("woff"), url("../../fonts/PTSans/pt-sans.ttf") format("truetype"), url("../../fonts/PTSans/pt-sans.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'PTSans-Bold';
  src: url("../../fonts/PTSans-Bold/PTSans-Bold.eot");
  src: local("PTSans-Bold"), url("../../fonts/PTSans-Bold/PTSans-Bold.woff") format("woff"), url("../../fonts/PTSans-Bold/PTSans-Bold.ttf") format("truetype"), url("../../fonts/PTSans-Bold/PTSans-Bold.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

body, div {
  font-family: "PTSans", sans-serif;
  font-size: 16px;
  margin: 0px;
}

h2 {
  font-family: "PTSans-Bold", sans-serif;
  font-size: 1.6em;
}

.header-page {
  height: 681px;
  z-index: 4;
  background: url(img/header-page.png) top center no-repeat;
  min-width: 1250px;
}

.header-page__layout {
  height: 133px;
  background: url(img/header.png) top center repeat;
  z-index: 5;
  width: 100%;
  min-width: 1200px;
}

.header-page__main {
  margin: auto;
  width: 1200px;
}

.logo, .logo__layer {
  height: 143px;
  width: 168px;
  background: url(img/logo-wrapper.png) top center no-repeat;
  z-index: 6;
  display: inline-block;
}

.logo__layer {
  background: url(img/logo-layer.png) top center no-repeat, url(img/logo-row.png) bottom center no-repeat;
}

.logo__row {
  /*width:165px;
	height: 10px;
	background: url(img/logo-row.png) top center no-repeat;*/
}

.logo__layer img {
  width: 119px;
  height: 101px;
  margin: 0 auto;
  display: block;
  padding-top: 11px;
  text-align: center;
}

.header-center {
  display: inline-block;
  vertical-align: top;
  margin-top: 20px;
  margin-left: 20px;
}

.menu-nav ul {
  margin: 0;
  padding: 4px;
}

.menu-nav ul li {
  font-family: "Gilroy-Bold", sans-serif;
  font-size: 0.8em;
  display: inline;
  margin-right: 5px;
  padding: 3px;
  color: white;
}

.menu-nav ul li span {
  margin-left: 10px;
}

.menu-nav ul li span:first-child {
  margin-left: 0px;
}

.header-info {
  margin-top: 30px;
}

.header-info__search {
  display: inline-block;
  vertical-align: top;
}

.header-info__search input {
  vertical-align: center;
  width: 250px;
  height: 30px;
  border-style: solid none;
  border-width: 0px 0px 3px 0px;
  border-color: #F1B814;
  -webkit-border-radius: 6px;
  -mox-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
}

.header-info__search button {
  vertical-align: bottom;
  background: url(img/search-button.png) top center no-repeat;
  width: 36px;
  height: 36px;
  text-decoration: none;
  display: inline-block;
  border-style: solid none;
  border-width: 0px 0px 3px 0px;
  border-color: #F1B814;
  -webkit-border-radius: 6px;
  -mox-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
}

.header-info__search input {
  padding-left: 10px;
}

.header-info__search .search-button img {
  text-align: center;
}

.header-info__address {
  display: inline-block;
  margin-left: 50px;
  vertical-allign: middle;
  font-family: "PTSans", sans-serif;
  font-weight: 400;
  font-size: 0.9em;
}

.header-info__address span {
  margin-left: 10px;
  color: white;
}

.header-info__address span a {
  color: white;
  font-weight: 200;
}

.header-info__address span img {
  vertical-align: middle;
}

.header-phone {
  font-family: "Gilroy", sans-serif;
  display: inline-block;
  vertical-align: top;
  text-align: right;
  margin-right: 5px;
  color: white;
  font-size: 1em;
  background: url(img/header-right-img.png) top right no-repeat;
  z-index: 10;
  height: 133px;
  float: right;
}

.header-phone div {
  display: inline-block;
  margin-top: 10px;
  margin-right: 40px;
  text-align: right;
  font-size: 1em;
}

.header-phone div span {
  font-size: 1.5em;
}

.header-phone div span span {
  font-size: 1em;
  font-family: "Gilroy-Bold", sans-serif;
}

.header-phone .back-call {
  display: inline-block;
  vertical-align: top;
  background: url(img/header-phone-btn.png) top center no-repeat;
  width: 166px;
  height: 37px;
  text-decoration: none;
  border-style: solid none;
  border-width: 0px 0px 3px 0px;
  border-color: #F1B814;
  -webkit-border-radius: 6px;
  -mox-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
  margin-top: 15px;
  font-size: 0.83em;
  color: #4C4C4C;
}

.cart {
  font-family: "PTSans-Bold", sans-serif;
  display: inline-block;
  vertical-align: top;
  background: url(img/header-phone-btn.png) top center no-repeat;
  height: 37px;
  text-decoration: none;
  border-style: solid none;
  border-width: 0px 0px 3px 0px;
  border-color: #F1B814;
  -webkit-border-radius: 6px;
  -mox-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
  margin-top: 15px;
  font-size: 0.83em;
  color: #4C4C4C;
