.menu-left-wrapper {
  position: absolute;
  padding-left: 15px;
  width: 263px;
  z-index: 1000;
  font-family: $font-sans-bold;
}

.menu-left {
  position: relative;
  background-color: white;
  z-index: 10;
  margin: 0;
  @include box-shadow(0, 10px, 10px, rgba(0,0,0,0.5));
  font-family: $font-sans-bold;
}

.menu-left__header {
  font-size: 1em;
  padding-left: 25px;
  font-weight: bold;
  background: url(img/menu-left-layout-first.png) no-repeat right bottom, url(img/menu-left-arrow.png) no-repeat left top;
  line-height: 3.5;
  font-family: $font-sans-bold;
}

.menu-left__toggle {
  font-size: 1em;
  padding-left: 25px;
  font-weight: bold;
  line-height: 3.5;
  border-top: 1px dashed lightgrey;
  background: url(img/menu-arrow.png) no-repeat right 10px center;
  font-family: $font-sans-bold;
}

.rollup {
  background: url(img/menu-arrow-rotate.png) no-repeat right 10px center;
}

.menu-left ul {
  margin: 0;
  padding: 0;
  vertical-align: middle;
}

.menu-left ul li {
  list-style: none;
  padding-left: 25px;
  font-size: 0.75em;
  line-height: 2.5;
  text-transform: uppercase;
  border-bottom: 1px dashed lightgrey;
  z-index: 12;
  position: relative;
  font-weight: bold;
}

.menu-left ul li:before {
  content: '';
  background: url(img/menu-left-list.png) no-repeat left top;
  height: 20px;
  width: 20px;
  position: absolute;
  left: 10px;
  top: 13px;
}

.menu-left ul li:nth-child(even) {
  background-color: #F3F4F6;
}

.menu-left ul li:last-child() {
   border-bottom: none;
 }

.menu-left ul li:first-child() {

}
